* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style: none;

  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
a {
  color: black;
  text-decoration: none;
}
.r {
  font-size: 9px;
  display: none;
}

/* Nav style */
.nav_container {
  height: 15vh;
  border-bottom: 2px solid rgb(236, 226, 226);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.left_nav,
.right_nav {
  height: 15vh;
  width: 30%;
}
.right_nav {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.nav_li {
  margin-right: 10px;
}
.location_div {
  width: 30%;
  height: 100%;
}
.nav_btn_div {
  width: 30%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 10px;
}
.left_nav ul,
.right_nav ul {
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

li {
  color: rgb(39, 38, 38);
  font-weight: bold;
}
li:hover {
  color: rgb(154, 224, 154);
}
.signin_btn {
  border: 1px solid black;
  border-radius: 20px;
  height: 35px;
  width: 75px;
  font-weight: bold;
  margin-right: 20px;
  background-color: white;
}

.signin_btn:hover {
  background-color: rgb(216, 211, 211);
}
.joinnow_btn:hover {
  background-color: rgb(158, 142, 142);
}
.joinnow_btn {
  border: 1px solid black;
  border-radius: 20px;
  height: 35px;
  width: 95px;
  font-weight: bold;
  color: white;
  background-color: black;
}
.hamburger_div {
  display: none;
}
/* sectionA style */
.sectionA_div,
.sectionB_div,
.sectionC_div,
.sectionD_div {
  height: 90vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sectionA_image_div,
.sectionB_image_div,
.sectionC_image_div,
.sectionD_image_div {
  height: 80vh;
  width: 50%;
  background-color: black;
}

.sectionA_img,
.sectionB_img,
.sectionC_img,
.sectionD_img {
  width: 100%;
  height: 100%;
}
.sectionA_text_div,
.sectionB_text_div,
.sectionC_text_div,
.sectionD_text_div {
  height: 80vh;
  width: 50%;
  background-color: #d4e9e2;
  padding: 0px 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.sectionA_text_div h1,
.sectionB_text_div h1,
.sectionC_text_div h1,
.sectionD_text_div h1 {
  font-size: 60px;
  text-align: center;
  color: #1e3932;
}
.sectionA_text_div h2,
.sectionB_text_div h2,
.sectionC_text_div h2,
.sectionD_text_div h2 {
  font-size: 22px;
  text-align: center;
  color: #1e3932;
  word-spacing: 3px;
  letter-spacing: 1.5px;
  font-weight: 500;
}
.sectionA_text_div button,
.sectionB_text_div button,
.sectionC_text_div button,
.sectionD_text_div button {
  height: 30px;
  width: 100px;
  margin-top: 30px;
  border-radius: 20px;
  font-weight: bold;
  color: #1e3932;
}
/* section b modification */
.sectionB_div {
  height: 65vh;
  margin-bottom: 5vh;
  background-color: #d4e9e2;
}
.sectionB_image_div {
  height: 54vh;
}
.sectionB_text_div {
  height: 60vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: start;
}
.sectionB_text_div h3 {
  font-size: 22px;
  color: #1e3932;
  margin-right: auto;
  font-weight: 700px;
}
.sectionB_text_div p {
  font-size: 22px;
  font-weight: 500px;
  word-spacing: 0.5px;
  color: #1e3932;
}
.sectionB_text_div ul li {
  font-size: 20px;
  font-weight: 400;
  margin: 5px 0px;
  list-style: disc;

  color: #1e3932;
}
.sectionB_text_div button {
  margin-right: auto;
  width: 120px;
}
/* Modification in section c */
.sectionC_div {
  height: 80vh;
}
.sectionC_text_div h2,
.sectionD_text_div h2 {
  word-spacing: 2.8px;
  color: #ffffff;
}
.sectionC_text_div h1,
.sectionD_text_div h1 {
  word-spacing: 2.8px;
  letter-spacing: 1.5px;
  color: #ffffff;
  margin-bottom: 20px;
}
.sectionD_text_div h1 {
  font-size: 50px;
}
.sectionC_text_div,
.sectionD_text_div {
  background-color: #006241;
}
.sectionC_text_div button,
.sectionD_text_div button {
  background-color: #006241;
  border: 2px solid #ffffff;
  color: #ffffff;
}
/* .sectionC_text_div button :hover {
  background-color: #74a192;
} */
/* About us style */
.aboutus_div {
  height: 60vh;
  width: 100%;
  border-bottom: 2px solid rgb(236, 226, 226);
  margin-bottom: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.about_us_topdiv {
  height: 20vh;
  width: 60%;
  padding: 0px 30px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.about_us_topdiv h5 {
  font-size: 18px;
  font-weight: 300px;
}
.about_us_topdiv h6 {
  font-size: 16px;
  font-weight: 300px;
}
.about_us_bottomdiv {
  height: 20vh;
  width: 60%;
  padding: 10px 30px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.about_us_bottomdiv h5 {
  font-size: 16px;
  font-weight: 300px;
}

/* style for fotterFirst */
.fotterFirst_div {
  border-bottom: 2px solid rgb(236, 226, 226);
  margin-bottom: 5vh;
  height: auto;
}
.down_arrow {
  display: none;
}
.fotterFirst_div div h5 {
  text-align: center;
  margin-bottom: 20px;
}
.fotterFirst_div div {
  text-align: left;
}
.fotterFirst_div div ul li {
  margin-bottom: 40px;
  font-size: 16px;
  font-weight: 100px;
  letter-spacing: 1px;
  color: #858a87;
  cursor: pointer;
}

.fotter_First_ul:hover {
  color: black;
}
/* last fotter style */
.social_media_icons {
  margin-top: 10px;
}
.fotter_a ul {
  width: 60%;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.fotter_a ul li {
  padding-right: 20px;
  border-right: 2px solid black;
  font-weight: 500;
  cursor: pointer;
}

.copywrite_div {
  width: 100%;
  height: 5vh;
  padding-left: 0px;
}
@media screen and (max-width: 1155px) {
  .nav_btn_div {
    width: 40%;
  }
  .location_div {
    width: 40%;
  }
  .left_nav {
    width: 40%;
  }
  .sectionA_text_div h1 {
    font-size: 40px;
  }
  .sectionB_div {
    height: 80vh;
  }
}
@media screen and (max-width: 948px) {
  .nav_btn_div {
    width: 50%;
  }
  .location_div {
    width: 50%;
  }
  .left_nav {
    width: 50%;
  }
  .sectionA_text_div {
    padding: 0px;
  }
  .sectionA_text_div h1 {
    font-size: 25px;
  }
  .sectionA_div h2 {
    font-size: 18px;
  }
  .sectionB_div {
    height: 90vh;
  }
  .sectionB_text_div p {
    word-spacing: 0px;
    font-size: 16px;
  }
  .sectionB_text_div ul li {
    word-spacing: 0px;
    font-size: 16px;
  }
  .sectionD_text_div h1 {
    font-size: 38px;
  }
  .fotter_a ul {
    height: auto;
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px 0px;
  }
  .fotter_a ul li {
    margin-bottom: 10px;
    border-right: none;
  }
  .social_media_icons {
    margin-top: 10px;
    height: 10vh;
    width: 100%;
    line-height: 10vh;
    padding-left: 0px;
  }
  .fotter_copywrite {
    width: 100%;
  }
  .fotter_row_div {
    height: 50vh;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
  }
  .fotterFirst_div {
    height: auto;
  }
  .fotter_row_div {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: 80vh;
  }
  .fotter_row_div div {
    padding: 0px 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .fotter_dropdown {
    display: block;
  }
  .li {
    display: none;
  }
  .down_arrow {
    display: block;
  }
  .fotterFirst_div div ul li {
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 750px) {
  .nav_container {
    height: 10vh;
  }
  .logo {
    font-size: 20px;
  }
  .hamburger_div {
    display: block;
  }
  .a {
    display: none;
  }
  .left_nav {
    width: 15%;
  }
  .right_nav {
    display: none;
  }
  .hamburger_div {
    margin-right: 40px;
  }
  .sectionA_div,
  .sectionB_div,
  .sectionC_div,
  .sectionD_div {
    height: auto;
    flex-direction: column;
  }
  .sectionA_div {
    margin-bottom: 5vh;
  }
  .sectionA_text_div h1 {
    font-size: 25px;
    font-weight: 500px;
  }
  .sectionA_text_div {
    width: 100%;
    height: auto;
    padding: 20px 20px;
  }
  .sectionA_image_div {
    width: 100%;
  }
  .sectionB_text_div {
    width: 100%;
    height: auto;
    padding: 20px 20px;
  }
  .sectionB_image_div {
    width: 100%;
    order: -2;
  }
  .sectionC_image_div {
    width: 100%;
  }

  .sectionC_text_div {
    width: 100%;
    height: auto;
    padding: 20px 20px;
  }
  .sectionD_div {
    height: auto;
  }
  .sectionD_image_div {
    width: 100%;
    height: auto;
    order: -2;
  }
  .sectionD_image_div img {
    height: 100%;
  }
  .sectionD_text_div {
    width: 100%;
    height: auto;
    padding: 20px 20px;
  }
  .aboutus_div {
    height: auto;
  }
  .about_us_bottomdiv,
  .about_us_topdiv {
    height: auto;
    width: 100%;
    padding: 20px 20px;
  }
}
@media screen and (max-width: 400px) {
  .fotter_copywrite {
    font-size: 11px;
  }
}
